import GetMongo from "../../cf-utils/mongodb"
import { GoogleGenerativeAI } from "@google/generative-ai"
import { RequestMD } from "../../cf-utils/request"
import { ttsmap } from "../../cf-utils/ttsmap"
import upload from "../../cf-utils/upload"
import md5 from "md5"

export async function onRequestPost(context) {
	const { request } = context
	const json = await request.json()
	const topic = json.topic ?? "Generate episode"

	//load up the model
	// console.log(context.env.GOOGLE_API_KEY)
	const genai = new GoogleGenerativeAI(context.env.GOOGLE_API_KEY)

	const model = genai.getGenerativeModel({
		model: "gemini-1.5-pro-latest",
	})

	//populate the message for the ai
	const prompt_parts = ["SYSTEM: " + RequestMD, "\n\nUSER: ", topic]
	const response = await model.generateContent(prompt_parts, {
		temperature: 0.4,
		topK: 32,
		topP: 1,
		maxOutputTokens: 8192,
	})
	const text = response.response
		.text()
		.replace("```json", "")
		.replace("\n```", "")
		.trim()
	console.log(text)
	//convert to json
	const script = JSON.parse(text)
	const url = md5(text)

	//upload script to mongo
	const { scripts_collection } = await GetMongo(context)
	const res = await scripts_collection.insertOne({
		url,
		script,
		views: 0,
	})

	for (const entry of script.timeline) {
		const action = entry.action
		let character = action.character
		if (character) {
			character = entry.character
		}
		const dialogue = action.dialogue
		if (dialogue && character) {
			console.log("Generating audio for character", character, dialogue)
			const hash = md5(dialogue)
			const fn = `${hash}.mp3`
			const ttsv = ttsmap[character.toLowerCase()]

			//generate tts from elevenlabs via api call
			const response = await fetch(
				"https://api.elevenlabs.io/v1/text-to-speech/" + ttsv.id,
				{
					headers: {
						"xi-api-key": context.env.ELEVENLABS_API_KEY,
					},
					method: "POST",
					body: JSON.stringify({
						text: dialogue,
					}),
				}
			)

			//parse the results
			const audio = await response.blob()

			//upload audio to s3
			await upload(context, context.env.S3_BUCKET_NAME, fn, audio, "audio/mp3")
			action.audio_file = fn
			action.audio_cf = true
		}
	}

	console.log("Updating entry")
	await scripts_collection.updateOne(
		{ _id: res.insertedId },
		{ $set: { script } }
	)
	console.log("Done")

	for (const entry of script.timeline) {
		const action = entry.action
		if (action.audio_s3) {
			action.audio_url =
				"https://bucket.housereaize.myapp.sh/housereaize/" + action.audio_file
		}
	}

	return Response.json({ _id: res.insertedId, script, url, views: 0 })
}
